// DataContext.js
import React, { createContext, useState } from "react";

const DataContext = createContext();

const DataContextProvider = ({ children }) => {
  const [data, setData] = useState(); // Khi to state v setter function cho data

  return (
    <DataContext.Provider value={{ data, setData }}>
      {" "}
      {/* Provider cung cp data v setData */}
      {children}
    </DataContext.Provider>
  );
};

export { DataContext, DataContextProvider }; // ci ny ko cn quan tm , y l ni trung truyn d liu ca cc page
